import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  searchterm: "",
  searchlist: [],
  watchlist: [],
}

export const watchlistslice = createSlice({
  name: 'watchlist',
  initialState,
  reducers: {
    // increment: (state) => {
    //   // Redux Toolkit allows us to write "mutating" logic in reducers. It
    //   // doesn't actually mutate the state because it uses the Immer library,
    //   // which detects changes to a "draft state" and produces a brand new
    //   // immutable state based off those changes
    //   state.value += 1
    // },
    // decrement: (state) => {
    //   state.value -= 1
    // },
    setsearchterm: (state, action) => {
      state.setsearchterm = action.payload;
    },
    setsearchlistR: (state, action) => {
      state.searchlist = action.payload
    },
    setwatchlistR: (state, action) => {
    //   console.log("Action Called: ", action.payload);
      state.watchlist = action.payload
    },
  },
})

// Action creators are generated for each case reducer function
export const { setsearchterm, setsearchlistR, setwatchlistR } = watchlistslice.actions

export default watchlistslice.reducer